.Nevigation{
    color: #01358D;
    font-weight: 600;
    font-size: 16px;
    line-height: 90px;
    font-family: sans-serif;
    margin-left: 50px;
    transition: all .3s ease-in-out;
}

.Nevigation:hover{
    cursor: pointer;
    text-decoration: underline;
    color: #f9556d;   
}

